#footer {
    padding-top: 100px;
    padding-bottom: 30px;
    .footer {
        &__inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20pxpx;
        }

        &__privacy {
            font-family: var(--Ubuntu);
            font-size: 14px;
            font-weight: 400;
            line-height: 18.2px;
            text-align: left;
            text-decoration-line: underline;
            text-decoration-style: solid;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            text-decoration: underline;
             color: var(--mainText);
        }

        &__dev {
        }

        &__copyright {
            font-family: var(--Ubuntu);
            font-size: 14px;
            font-weight: 400;
            line-height: 21px;
            text-align: right;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--mainText);
        }
    }
}


@media screen and (max-width: 1660px) {

    #footer {
        padding-top: 30px;
    }
}

@media screen and (max-width: 873px) {
    #footer .footer__inner {
        flex-direction: column;
        gap: 20px;
    }
}