#contacts {
    position: relative;
    background: var(--bgLight);
    .contacts {
        &__inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 200px;
        }

        &__left {
            flex-basis: 50%;
        }

        &__title {
            text-transform: uppercase;
            margin-bottom: 60px;
        }

        &__list {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            align-items: center;
            gap: 60px;
        }

        &__item {
        }

        &__phone {
            color: var(--head);
            display: block;
            margin-bottom: 10px;
        }

        &__time {
            font-family: Ubuntu;
            font-size: 14px;
            font-weight: 400;
            line-height: 18.2px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--mainText);
        }

        &__email {
            font-family: Onest;
            font-size: 20px;
            font-weight: 400;
            line-height: 24px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--head);
        }

        &__requisites {
            font-family: Ubuntu;
            font-size: 14px;
            font-weight: 400;
            line-height: 21px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--mainText);
            margin-bottom: 10px;
        }

        &__name {
            font-family: Ubuntu;
            font-size: 14px;
            font-weight: 400;
            line-height: 18.2px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--mainText);
            margin-bottom: 10px;
            &-social {
                margin-bottom: 30px;
            }
        }

        &__socials {
            display: flex;
            align-items: center;
            gap: 10px;
        }

        &__soc-link {
            width: 54px;
            height: 54px;
            border-radius: 50%;
            background: var(--primary);
            display: flex;
            justify-content: center;
            align-items: center;
            gap: px;
            img, svg {
                width: 24px;
                height: 24px;
            }
        }

        &__img {
            flex-basis: 100%;
            height: 100%;
            position: absolute;
            right: 0;
            top: 100px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    .container {
    }
    .cotacts {
        &__name {
        }
    }
    .h4 {
    }
}


@media screen and (max-width: 1660px) {
#contacts .contacts__img {
    position: static;
    flex-basis: 50%;
}
#contacts .contacts__inner {
    gap: 20px;
}
}


@media screen and (max-width: 1260px) {
#contacts .contacts__inner {
    flex-direction: column;
}
}


@media screen and (max-width: 640px) {
#contacts .contacts__list {
    grid-template-columns: 1fr;
    gap: 20px;
    justify-content: center;
    max-width: fit-content;
    align-items: center;
    margin: 0px auto;
}
#contacts .contacts__title {
    text-align: center;
}
#contacts .contacts__item {
    max-width: fit-content;
}

}