#header {
    padding: 20px 0;
    border-bottom: 1px solid var(--bgDark);
    .header {
        &__wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
        }

        &__left {
            display: flex;
            align-items: center;
            gap: 78px;
        }

        &__logo {
        }

        &__adress {
            display: flex;
            align-items: center;
            gap: 10px;
        }

        &__right {
            display: flex;
            align-items: center;
            gap: 72px;
        }

        &__nav {
            display: flex;
            align-items: center;
            gap: 30px;
        }

        &__item {
        }

        &__link {
            font-family: var(--fontFamily);
            text-transform: uppercase;
            font-size: 14px;
            font-weight: 500;
            line-height: 19.6px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--mainText);
        }

        &__contacts {
            display: flex;
            flex-direction: column;
            gap: 5px;
        }

        &__phone {
        }

        &__burger {
            display: none;
        }

        &__contacts-link {
            font-family: var(--Ubuntu);
            font-size: 14px;
            font-weight: 400;
            line-height: 18.2px;
            text-align: left;
            text-decoration-line: underline;
            text-decoration-style: solid;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            text-decoration: underline;
        }
    }
    .mobile {
        &__menu {
            transition: 0.3s all ease-in-out;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            background: var(--bgDark);
            display: flex;
            flex-direction: column;
            padding: 20px;
            z-index: 3;
            left: -300px;
            &.active {
                left: 0;
            }
            .header__logo {
                margin-bottom: 20px;
            }
            .header__nav {
                flex-direction: column;
                margin-bottom: 30px;
            }
        }
    }

    .socials__right {
        position: fixed;
        right: 40px;
        top: calc(288px + 91px);
        z-index: 10;
    }
    .soc-link {
        margin-bottom: 10px;
        width: 54px;
        height: 54px;
        border-radius: 50%;
        background: var(--primary);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.3s ease-in-out background-color;
        cursor: pointer;
        &:hover {
            background: var(--hover);
        }
        img,
        svg {
            width: 24px;
            height: 24px;
        }
    }
}

@media screen and (max-width: 1350px) {
    #header .header__left {
        gap: 30px;
    }
}

@media screen and (max-width: 1293px) {
    #header .header__nav {
        display: none;
    }
    #header .mobile__menu .header__nav {
        display: flex;
    }
    #header .header__burger {
        display: block;
    }
}

@media screen and (max-width: 630px) {
    #header .header__wrapper .header__contacts {
        display: none;
    }
    #header .socials__right {
        top: unset;
        bottom: 40px;
        right: 20px;
    }
}
@media screen and (max-width: 370px) {
    #header .header__adress {
        display: none;
    }
}



