#hero {
    padding: 0;
    .hero {
        &__container {
            max-width: unset;
            display: flex;
        }

        &__top {
            padding-top: 130px;
            padding-left: 145px;
            padding-right: 140px;
        }

        &__title {
            margin-bottom: 30px;
            font-family: var(--SweetMavkaScript);
            font-size: 86px;
            font-weight: 500;
            line-height: 86px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--primary);
            max-width: 560px;
        }

        &__descr {
            margin-bottom: 40px;
            max-width: 560px;
            color: var(--mainText);
        }

        &__btn {
        }
        &__text {
            display: flex;
            flex-direction: column;

        }

        &__bottom {
            margin-top: auto;
            padding: 20px 0;
            padding-left: 145px;
            border-top: 1px solid var(--bgDark);
        }

        &__key {
            margin-bottom: 5px;
            color: var(--Head);
        }

        &__value {
            color: var(--mainText);
        }
        &__swiper {
            position: relative;
            width: 100%;
            padding-right: 75px;
            gap: 50px;
            min-width: 0;
            flex-grow: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &__btns {
            position: absolute;
            bottom: 91px;
            left: 535px;
            z-index: 2;
            display: flex;
            gap: 1px;
        }
        &__swiper-button-prev,
        &__swiper-button-next {
            height: 54px;
            width: 54px;
            position: static;
            inset: unset;
        
            &::after {
                content: none;
            }
        }
        
    }

    .main-swiper {
        overflow: hidden;
        min-width: 590px;
        height: 100%;
        .swiper-slide {
            height: 100%;
            img {
                height: 100%;
                max-width: 590px;
                width: 100%;
            }
        }
    }
    .thumb-swiper {
        height: 446px;
        .swiper-slide {
            height: 100%;
            img {
                height: 100%;
                max-width: 285px;
            }
        }
    }
}


@media screen and (max-width: 1820px) {
#hero .thumb-swiper {
    display: none;
}
}

@media screen and (max-width: 1440px) {
#hero .hero__top {
    padding: 40px;
    padding-right: 80px;
}
}

@media screen and (max-width: 1290px) {
#hero .hero__container {
    flex-direction: column;
}
#hero .hero__title,
#hero .hero__descr
{
 max-width: unset;
}
#hero .hero__bottom {
    padding-left: 40px;
}
}

@media screen and (max-width: 700px) {
#hero .hero__title {
    font-size: 48px;
    line-height: 110%;
}
#hero .main-swiper {
 min-width: 320px;
}
#hero .hero__btns {
    left: 0;
    bottom: 40px;
}
}