#products {
    .products {
        &__top {
            display: flex;
            align-items: center;
            gap: 210px;
            margin-bottom: 50px;
        }

        &__name {
            text-transform: uppercase;
            text-align: center;
            padding-bottom: 21px;
            border-bottom: 1px solid var(--bgDark);
        }

        &__tab {
            display: flex;
            align-items: center;
            gap: 20px;
        }
        &__bottom {
            padding-top: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &__tab-item {
            text-transform: uppercase;
            border-radius: 30px;
            padding: 18px 35px;
            cursor: pointer;
            transition: 0.3s all ease-in-out;
            border: 1px solid var(--stroke);
            color: var(--mainText);
            &.active, &:hover {
                color: var(--card);
                background: var(--stroke);
            }
            
        }

        &__list {
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            align-items: center;
            gap: 50px;
        }

        &__item {
            display: none;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            &.wide {
                grid-column: 2 span;
                height: 100%;
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                border: 1px solid var(--stroke);
                .products__name {
                    font-size: 24px;
                    font-weight: 500;
                    line-height: 36px;
                    border: none;
                }
                .products__img {
                    max-height: 100%;
                    height: 100%;
                }
                .products__content {
                    height: 100%;
                    padding: 50px 40px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    flex-direction: column;
                }
                .products__bottom {
                    flex-direction: column;
                    gap: 30px;
                    width: 100%;
                }
                .products__prices {
                    flex-direction: row;
                    align-items: center;
                    gap: 15px;
                }
                .products__btn {
                    width: 100%;
                    display: flex;
                    justify-content: center;
                }
            }
        }

        &__img {
            padding: 15px;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            max-height: 382px;
            min-height: 382px;
            width: 100%;
            position: relative;
            img {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: -1;
            }
        }

        &__marks {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 5px;
        }

        &__mark {
            text-transform: uppercase;
            font-family: Ubuntu;
            font-size: 10px;
            font-weight: 700;
            line-height: 13px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            padding: 5px 10px;
            border-radius: 30px;
            color: var(--card);
            &.new {
                background: var(--label-1);
            }
            &.stock {
                background: var(--label-2);
            }
            &.hit {
                background: var(--label-3);
            }
        }

        &__more {
            position: relative;
            margin-top: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;
            background: var(--accent-1);
            border-radius: 50%;
            color: var(--card);
            transition: 0.3s ease-in-out background-color;
            &:hover {
                background: var(--hover);
            }
            &:hover {
                .products__more-content {
                    visibility: visible;
                }
            }
            &-content {
                z-index: 2;
                visibility: hidden;
                padding: 25px 30px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-10%, 15%);
                width: 520px;
                backdrop-filter: blur(100px);
                border: 1px solid var(--stroke);
                p {
                    color: var(--mainText);
                }
            }
        }

        &__content {
        }

        &__prices {
            display: flex;
            align-items: flex-start;
            gap: 5px;
            flex-direction: column-reverse;
        }

        &__price {
        }

        &__oldprice {
            font-family: Ubuntu;
            font-size: 14px;
            font-weight: 400;
            line-height: 18.2px;
            text-align: left;
            text-decoration-line: line-through;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--mainText);
        }

        &__btn {
        }
    }
    .container {
    }
    .new {
    }
    .stock {
    }
    .hit {
    }
    .products-more {
        &__content {
        }
    }
    .p3 {
    }
    .btn {
    }
}

@media screen and (max-width: 1404px) {
    #products .products__list {
        gap: 10px;
    }
}

@media screen and (max-width: 1250px) {
    #products .products__list {
        grid-template-columns: repeat(3, 1fr);
    }
    #products .products__top {
        gap: 20px;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}


@media screen and (max-width: 992px) {
     #products .products__list {
        grid-template-columns: repeat(2, 1fr);
    }
    #products .products__tab {
        gap: 10px;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}


@media screen and (max-width: 556px) {
    #products .products__list {
        grid-template-columns: 1fr;
    }
    #products .products__item.wide {
        grid-column: 1 span;
        grid-template-columns: 1fr;
    }
    #products .products__item.wide .products__content {
        padding: 10px;
    }
    #products .products__more-content {
        max-width: 320px;
        width: 320px;
    }
}