#production {
    position: relative;
    overflow: hidden;
    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 594px;
        background: var(--bgLight);
        position: absolute;
        bottom: 0;
        z-index: -1;
    }
    .production {
        &__top {
            display: flex;
            justify-content: space-between;
            gap: 120px;
            margin-bottom: 114px;
        }

        &__wrapper {
        }

        &__left {
            display: flex;
            flex-direction: column;
            gap: 40px;
        }

        &__title {
            max-width: 560px;
        }

        &__descr {
            max-width: 560px;
            margin-bottom: auto;
            color: var(--mainText);
        }

        &__img-wrapper {
            display: flex;
            align-items: center;
            gap: 50px;
        }

        &__img {
            width: 270px;
            height: 180px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__swiper {
            position: relative;
            flex-grow: 1;
            min-width: 0;
            
        }

        &__btns {
            position: absolute;
            height: 54px;
            width: fit-content;
            display: flex;
            align-items: center;
            gap: 5px;
            bottom: -5px;
            left: -58px;
            z-index: 4;
        }

        &__text-wrapper {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }

        &__text {
            &:first-child {
                .production__text-descr {
                    max-width: 510px;
                }
            }
        }

        &__text-title {
            color: var(--accent-1);
            margin-bottom: 10px;
            text-transform: uppercase;
        }

        &__text-descr {
            max-width: 790px;
            color: var(--mainText);
        }
    }
    .container {
    }
    .p2 {
    }
    .production-swiper {
        overflow: hidden;
    }
    .swiper-wrapper {
        
    }
    .swiper-slide {
        
    }
    .swiper-button-prev {
        position: static;
        width: 54px;
        height: 54px;
        &::after {
            content: none;
        }
    }
    .swiper-button-next {
        position: static;
        width: 54px;
        height: 54px;
        &::after {
            content: none;
        }
    }
}

@media screen and (max-width: 1450px) {
    #production .production__top {
        gap: 70px;
    }
}


@media screen and (max-width: 1376px) {
#production .production__top {
    flex-direction: column;
}
#production .production__btns {
    left: unset;
    right: 0;
}
}

@media screen and (max-width: 594px) {
#production .production__text-wrapper {
    grid-template-columns: 1fr;
    gap: 20px;
}
}