#shop {
    .shop {
        &__title {
            text-transform: uppercase;
            margin-bottom: 60px;
        }

        &__list {
            display: flex;
            align-items: center;
            gap: 60px;
            margin-bottom: 60px;
        }

        &__item {
            display: flex;
            align-items: center;
            gap: 30px;
            position: relative;
        }

        &__item-img {
            max-width: 90px;
            max-height: 90px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__name {
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            gap: 15px;
            font-family: Onest;
            font-size: 34px;
            font-weight: 400;
            line-height: 44.2px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
        }

        &__name-link {
            &::after {
                content: "";
                width: 100%;
                height: 100%;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
            text-transform: uppercase;
            color: var(--primary);
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &__places {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            align-items: center;
            margin-bottom: 50px;
            border-top: 1px solid var(--bgDark);
        }
        &__link-more {
            display: block;
            max-width: fit-content;
            margin: 0px auto;
        }
    }
    .container {
    }
    .places {
        &__item {
            padding: 60px;
            padding-right: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 30px;
            border-bottom: 1px solid var(--bgDark);
            &:first-child,
            &:nth-child(3) {
                padding-left: 0;
                padding-right: 60px;
                border-right: 1px solid var(--bgDark);
            }
        }

        &__title {
            max-width: 290px;
            text-transform: uppercase;
        }

        &__right {
        }

        &__name {
            display: block;
            margin-bottom: 5px;
        }

        &__descr {
        }
    }
    .p1 {
    }
    .p2 {
    }
}

@media screen and (max-width: 1610px) {
    #shop .shop__places {
        grid-template-columns: 1fr;
    }
    #shop .places {
        &__item {
            padding: 60px !important;
        }
    }
}

@media screen and (max-width: 1100px) {
    #shop .shop__list {
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 710px) {
    #shop .places__item {
        padding: 20px !important;
    }
}

@media screen and (max-width: 550px) {
    #shop .places__item {
        flex-direction: column;
        gap: 10px;
        align-items: flex-start;
    }
}