#about {
    padding: 0;
    position: relative;
    .about {
        &__left {
            padding: 100px 0;
            max-width: 755px;
            padding-right: 45px;
            height: 100%;
            border-right: 1px solid var(--bgDark);
        }

        &__inner {
            display: flex;
        }
        &__title {
            text-transform: uppercase;
            max-width: 560px;
            margin-bottom: 25px;
        }

        &__title-img {
        }

        &__right {
            padding: 100px 0;
            padding-left: 90px;
        }

        &__subtitle {
            font-family: var(--SweetMavkaScript);
            font-size: 26px;
            font-weight: 500;
            line-height: 31.2px;
            text-align: left;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--primary);
            margin-bottom: 25px;
        }

        &__descr {
            max-width: 650px;
            margin-bottom: 30px;
        }

        &__link {
            text-transform: uppercase;
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &__link-text {
        }

        &__img {
            position: absolute;
            max-width: 360px;
            max-height: 360px;
            right: 0;
            bottom: 100px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}


@media screen and (max-width: 1810px) {
#about .about__img {
    position: relative;
    bottom: unset;
    right: unset;
}
#about .about__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
}
}

@media screen and (max-width: 1500px) {

#about .about__right {
    flex-direction: column;
    align-items: flex-start;
}
}


@media screen and (max-width: 1150px) {
#about .about__left {
    padding-top: 40px;
    padding-bottom: 40px;
    padding-right: 20px;
}

#about .about__right {
    padding-top: 40px;
    padding-left: 20px;
    padding-bottom: 40px;
}
}


@media screen and (max-width: 765px) {
#about .about__inner {
    flex-direction: column;
}
#about .about__left {
    border-right: none;
    border-bottom: 1px solid var(--bgDark
    );
}
#about .about__title {
    text-align: center;
    max-width: unset;
}
#about .about__title-img {
    max-width: fit-content;
    margin: 0px auto;
}
}