#info {
    padding-top: 0;
    position: relative;
    .info {
        &__left {
            padding-top: 100px;
            padding-bottom: 80px;
            padding-right: 50px;
            border-right: 1px solid var(--bgDark);
        }

        &__wrapper {
            display: flex;
        }

        &__title {
            margin-bottom: 40px;
        }

        &__text {
            max-width: 560px;
            .p2 {
                margin-bottom: 20px;
                color: var(--mainText);
            }
            ul {
                padding: 0;
                margin: 0;
            }
            ul, li {
                list-style: none;
            }
            li {
                color: var(--mainText);
                display: flex;
                align-items: center;
                gap: 15px;
                margin-bottom: 15px;
                &::before {
                    content: '';
                    display: block;
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background: var(--accent-1);
                }
            }
        }

        &__right {
            padding-top: 100px;
            padding-left: 90px;
        }

        &__img-1 {
            width: 845px;
            height: 400px;
            // position: absolute;
            // left: 0;
            // bottom: 100px;
            svg,
            Img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__img-2 {
            height: 665px;
            width: 425px;
            position: absolute;
            right: 0;
            top: 100px;
            z-index: -2;
            svg,
            Img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__bg {
            width: 700px;
            height: 420px;
            position: absolute;
            z-index: -3;
            right: 0;
            bottom: 100px;
            svg,
            Img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    .container {
    }
}


@media screen and (max-width: 1770px) {
#info .info__img-2 {
    position: static;
    margin-top: 20px;
}
}
@media screen and (max-width: 850px) {
#info .info__left {
    padding-right: 20px;
}
#info .info__right {
    padding-left: 20px;
}
#info .info__img-1 {
    width: 100%;
}
}
@media screen and (max-width: 760px) {
#info .info__wrapper {
    flex-direction: column;
}
#info .info__left {
    padding-bottom: 0;
    border: none;
}
#info .info__right {
    padding-left: 0;
    padding-top: 20px;
}

}
@media screen and (max-width: 468px) {
#info .info__img-2{
    width: 100%;
}
}