#partners {
    .partners {
        &__inner {
            display: flex;
            align-items: center;
            gap: 50px;
            min-width: 0;
        }

        &__left {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            flex-direction: column;
            height: 100%;
        }

        &__title {
            text-transform: uppercase;
            margin-bottom: 70px;
        }

        &__btns {
            display: flex;
            align-items: center;
            gap: 1px;
            height: 54px;
        }
        &__swiper-button-next,
        &__swiper-button-prev {
            width: 54px;
            height: 54px;
            img, svg {
                width: 100%;
                height: 100%;
            }
            position: static;
            &::after {
                content: none;
            }
        }

        &__right {
            flex-grow: 1;
            min-width: 0;
        }

        &__swiper {
        }
    }
    .container {
    }
    .swiper-button-prev {
    }
    .swiper-button-next {
    }
    .swiper {
    }
    .swiper-wrapper {
    }
    .swiper-slide {
        padding: 25px;
        border: 1px solid var(--bgDark);
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            
            width: 152px;
            height: 152px;
        }
    }
}


@media screen and (max-width: 1000px) {
#partners .partners__inner {
   display: block;
}
#partners .partners__title {
    margin-bottom: 20px;
}
#partners .partners__left {
    margin-bottom: 30px;
}
}