.p1 {
  //styleName: P1;
  font-family: var(--Ubuntu);
  font-size: 24px;
  font-weight: 300;
  line-height: 36px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  margin: 0;
}

.p2 {
  //styleName: P2;
  font-family: var(--Ubuntu);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  margin: 0;
}

.p3 {
  //styleName: P3;
  font-family: var(--Ubuntu);
  font-size: 14px;
  font-weight: 400;
  line-height: 18.2px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  margin: 0;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: var(--head);
  transition: 0.3s color ease-in-out;
  &:hover {
    color: var(--hover);
  }
}

section {
  padding: 100px 0;
  border-bottom: 1px solid var(--bgDark);
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

button {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  max-width: 1660px;
  padding: 0 15px;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  padding: 18px 35px;
  font-family: var(--Ubuntu);
  text-transform: uppercase;
  font-size: 14px;
  cursor: pointer;
  font-weight: 700;
  line-height: 18.2px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  border-radius: 30px;
  box-sizing: border-box;
  color: var(--card);
  background: var(--primary);
  transition: 0.3s all ease-in-out;
  &:hover {
    background: var(--hover);
  }
  &.invert {
    background: var(--card);
    color: var(--primary);
    border: 1px solid var(--primary);
  }
  &.invert:hover {
    color: var(--card);
    background: var(--primary);
  }
}



@media screen and (max-width: 1150px) {
  section {
    padding: 40px 0;
  }
}